.glass {
  background: rgba(20, 20, 20, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 59, 59, 0.1);
}
.glass-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.03) 0%, rgba(255, 255, 255, 0.01) 100%);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid rgba(255, 59, 59, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}
.glass-card:hover {
  background: linear-gradient(135deg, rgba(255, 59, 59, 0.05) 0%, rgba(255, 59, 59, 0.01) 100%);
  border-color: rgba(255, 59, 59, 0.3);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);
  transform: translateY(-4px) scale(1.01);
}
.scrollbar-thin::-webkit-scrollbar {
  width: 6px;
}
.scrollbar-thin::-webkit-scrollbar-track {
  background: transparent;
}
.scrollbar-thin::-webkit-scrollbar-thumb {
  background: rgba(255, 59, 59, 0.2);
  border-radius: 3px;
}
.scrollbar-thin::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 59, 59, 0.4);
}
.badge-live {
  background: rgba(34, 197, 94, 0.2);
  color: #4ade80;
  border: 1px solid rgba(34, 197, 94, 0.3);
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  animation: pulse-green 2s infinite;
}
@keyframes pulse-green {
  0%,
    100% {
    box-shadow: 0 0 0 rgba(34, 197, 94, 0);
  }
  50% {
    box-shadow: 0 0 10px rgba(34, 197, 94, 0.3);
  }
}
.terminal-log {
  font-family: 'JetBrains Mono', 'Fira Code', monospace;
  font-size: 11px;
  background: #0a0a0a;
  border: 1px solid rgba(255, 59, 59, 0.2);
  border-radius: 8px;
  padding: 12px;
}
.animated-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(to bottom,
            transparent 0%,
            rgba(255, 59, 59, 0.02) 50%,
            transparent 100%);
  background-size: 100% 200%;
  animation: move-bg 20s linear infinite;
  pointer-events: none;
}
@keyframes move-bg {
  0% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}
.bg-grid-animate {
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  z-index: -2;
  background-image: radial-gradient(circle, rgba(255, 59, 59, 0.05) 1px, transparent 1px);
  background-size: 40px 40px;
  animation: grid-scroll 60s linear infinite;
  pointer-events: none;
}
@keyframes grid-scroll {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-40px);
  }
}
